/* Banner.css */

.banner {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  border-radius: 25px;
}

.banner-image {
  width: 100%;
  max-width: 100%;
  border-radius: 25px;
  object-fit: cover;
  min-height: 111px;
}

.banner1 {
  filter: brightness(60%);
}

.banner2 {
  filter: brightness(70%);
}

.banner-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Montserrat', sans-serif;
  font-size: 48px;
  font-weight: bold;
  color: white;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  white-space: nowrap;
}

@media screen and (max-width: 960px) {
  .banner-text {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
  }

  .banner {
    margin-top: 19px;
  }

  .text-mobile-split {
    display: block;
  }
}

@media screen and (max-width: 320px) {
  .banner-text {
    font-size: 20px;
    line-height: 20px;
  }
}