/* Header.css */

.header {
  background-color: #FFFFFF;
  padding: 45px 0 50px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  height: 68px;
  width: 210.32px;
}

@media screen and (max-width: 800px) {
  .logo img {
    height: 46.88px;
    width: 145px;
  }

  .header {
    padding: 20px 0 0 0;
  }
}

.nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav ul li {
  display: inline-block;
}

.nav ul li a {
  width: 136px;
  height: 34px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 142.6%;
  display: flex;
  align-items: flex-end;
  text-align: right;
  color: #000000;
  flex: none;
  order: 0;
  flex-grow: 0;
  text-decoration: none;
}

@media screen and (max-width: 800px) {
  .nav ul li a {
    font-size: 12px;
    text-transform: uppercase;
    align-items: center;
    width: 100%;
    height: 17px;
  }

  .header {
    justify-content: unset;
  }

  .nav ul li:first-child a {
    padding-left: 43px;
    padding-right: 26px;
  }
}

@media screen and (max-width: 390px) {
  .nav ul li:first-child a {
    padding-left: 35px;
  }

  .nav ul li a {
    padding-left: 35px;
  }
}

@media screen and (max-width: 320px) {
  .nav ul li:first-child a {
    padding-left: 20px;
  }

  .nav ul li a {
    padding-left: 20px;
  }
}

.nav ul li a:hover {
  text-decoration-line: underline;
}

.nav ul li a.active {
  /* Define styles for the active link */
  text-decoration: underline;
  /* You can adjust other styles like color, font weight, etc. */
}
